import { useEffect, useState } from "react";
import { NavLink, useParams } from "react-router-dom";
import callFetch from "../../helpers/callFetch";
import dateFormat from "dateformat";

function LeaveShow() {
    let params = useParams();
    const [leave, setLeave] = useState({});

    useEffect(() => {
        callFetch("leaves/" + params.id, "GET", {}, null).then((res) => {
            if (!res.ok) return;
            res.data.details = JSON.parse(res.data.details);
            setLeave(res.data);
        });
    }, []);

    return <div className="row">
        <div className="col-12">
            <div className="card mb-4">
                <div className="card-header pb-0">
                    <div className="btn-row d-flex">
                        <h6>View Leave</h6>
                        <NavLink to="/leaves" className="btn bg-gradient-dark ms-auto">Back</NavLink>
                    </div>
                </div>
                <div className="card-body">
                    <div className="row">
                        <div className="col-6 col-md-6">
                            <label>
                                Leave Type
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{leave.leave_type?.name}</h6>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-6 col-md-6">
                            <label>
                                Leave From
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{dateFormat(leave.leave_from, "dd-mm-yyyy")}</h6>
                        </div>
                        <div className="col-6 col-md-6">
                            <label>
                                Leave To
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{dateFormat(leave.leave_to, "dd-mm-yyyy")}</h6>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12">
                            <label>
                                Leave Reason
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{leave.details?.details}</h6>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-6">
                            <label>Attachment</label>
                            <h6 className="text-sm text-secondary mb-0">
                                {leave.details?.attachment ? <p>
                                    <a className="btn btn-icon btn-2 btn-outline-info" style={{ fontSize: '25px' }} href={process.env.REACT_APP_STORAGE_URL + leave.details?.attachment} target="_blank">
                                        <i className="fas fa-file-lines"></i>
                                    </a>
                                </p> : 'N/A'}
                            </h6>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>;
}

export default LeaveShow;