import { useEffect, useState } from 'react';
import DataTable from 'react-data-table-component';
import dateFormat from "dateformat";
import callFetch from '../../helpers/callFetch';
import deleteAlert from "../../helpers/deleteAlert";
import { NavLink } from 'react-router-dom';

function AttendanceReconciliationIndexTable() {
    const [attendanceReconciliations, setAttendanceReconciliations] = useState([]);
    const [pageNumber, setPageNumber] = useState(1);
    const [refresh, setRefresh] = useState(0);

    const tableHeadings = [
        {
            name: 'Reconciliation',
            width: '145px',
            cell: row => <div>
                <span className="text-sm font-weight-bold">Type:</span>
                <span className="text-sm">{row.type}</span>
                <br />
                <span className="text-sm font-weight-bold">Date:</span>
                <span className="text-sm">{dateFormat(row.attendance_date, "dd-mm-yyyy")}</span>
            </div>
        },
        {
            name: 'Status',
            selector: row => <>
                {row.status === 'pending' && <span className="badge badge-sm bg-gradient-warning">Pending</span>}
                {row.status === 'accepted' && <span className="badge badge-sm bg-gradient-success">Accepted</span>}
                {row.status === 'rejected' && <span className="badge badge-sm bg-gradient-danger">Rejected</span>}
            </>
        },
        {
            name: 'Actions',
            width: '80px',
            cell: row => <div className="text-center dropstart">
                <a href="/" className="dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="fa fa-ellipsis-v text-xs"></i>
                </a>
                <ul className="dropdown-menu">
                    <li>
                        <NavLink to={'/attendance-reconciliations/' + row.id} className="dropdown-item">
                            View
                        </NavLink>
                    </li>
                    <li><hr className="dropdown-divider" /></li>
                    <li><a className="dropdown-item text-danger" href="#0" onClick={(e) => deleteAlert(e, 'attendance-reconciliations', row.id).then(res => setRefresh(refresh + 1))}>Delete</a></li>
                </ul>
            </div>
        }
    ];

    useEffect(() => {
        callFetch("attendance-reconciliations?page=" + pageNumber, "GET", []).then((res) => {
            setAttendanceReconciliations(res.data);
        });
    }, [pageNumber, refresh]);

    const handlePageChange = page => {
        setPageNumber(page);
    }

    function toPages(pages) {
        const results = [];

        for (let i = 1; i <= pages; i++) {
            if (i > 5) {
                results.push(pages);
                break;
            }

            results.push(i);
        }

        return results;
    }

    // RDT exposes the following internal pagination properties
    const BootyPagination = ({
        onChangePage,
        currentPage
    }) => {
        const handleBackButtonClick = () => {
            onChangePage(currentPage - 1);
        };

        const handleNextButtonClick = () => {
            onChangePage(currentPage + 1);
        };

        const handlePageNumber = (e) => {
            onChangePage(Number(e.target.value));
        };

        const pages = attendanceReconciliations.last_page;
        const pageItems = toPages(pages);
        const nextDisabled = currentPage === attendanceReconciliations.last_page;
        const previosDisabled = currentPage === 1;

        return (
            <>
                <br />
                <p className="float-md-start pt-2 text-secondary text-xs font-weight-bolder ms-3">Showing {attendanceReconciliations.from} to {attendanceReconciliations.to} of {attendanceReconciliations.total} entries</p>
                <nav className="float-md-end me-2">
                    <ul className="pagination">
                        <li className="page-item">
                            <button
                                className="page-link"
                                onClick={handleBackButtonClick}
                                disabled={previosDisabled}
                                aria-disabled={previosDisabled}
                                aria-label="previous page"
                            >
                                &#60;
                            </button>
                        </li>
                        {pageItems.map((page) => {
                            const className =
                                page === currentPage ? "page-item active" : "page-item";

                            return (
                                <li key={page} className={className}>
                                    <button
                                        className="page-link"
                                        onClick={handlePageNumber}
                                        value={page}
                                    >
                                        {page}
                                    </button>
                                </li>
                            );
                        })}
                        <li className="page-item">
                            <button
                                className="page-link"
                                onClick={handleNextButtonClick}
                                disabled={nextDisabled}
                                aria-disabled={nextDisabled}
                                aria-label="next page"
                            >
                                &#62;
                            </button>
                        </li>
                    </ul>
                </nav>
                <div className="clearfix"></div>
            </>
        );
    };

    return <DataTable
        columns={tableHeadings}
        data={attendanceReconciliations.data}
        noDataComponent='There are no records to display'
        pagination
        highlightOnHover
        paginationComponentOptions={{ noRowsPerPage: true }}
        paginationServer
        paginationTotalRows={attendanceReconciliations.total}
        onChangePage={handlePageChange}
        paginationComponent={BootyPagination}
    />;
}

export default AttendanceReconciliationIndexTable;