import { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";
import callFetch from "../helpers/callFetch";

function Dashboard() {
    const [dashCalendar, setDashCalendar] = useState(null);
    const [dashboard, setDashboard] = useState({});

    useEffect(() => {
        callFetch("dashboard", "GET", {}, null).then((res) => {
            if (!res.ok) return;
            setDashboard(res.data);
        });

        getCurrentDate();
    }, []);

    function getCurrentDate() {
        const daysOfWeek = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
        const monthsOfYear = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
        const currentDate = new Date();
        let tempCal = {};
        tempCal.day = String(currentDate.getDate()).padStart(2, '0');
        tempCal.dayName = daysOfWeek[currentDate.getDay()];
        tempCal.monthName = monthsOfYear[currentDate.getMonth()];
        tempCal.year = currentDate.getFullYear();
        setDashCalendar(tempCal);
    }

    return <>
        <div style={{ borderRadius: '0 0 100px 100px', paddingBottom: '7em', background: '#00a650', position: 'absolute', width: '100%', top: 0, left: 0 }}>
            <div className="d-flex p-4">
                <div className="avatar avatar-xl rounded-circle me-3">
                    <img src={dashboard?.user?.photo_url} style={{ width: '74px', height: '74px' }} alt="" />
                </div>
                <div className="d-flex align-items-start flex-column justify-content-center">
                    <h6 className="mb-0 text-bolder text-white">{dashboard?.user?.name}</h6>
                    <p className="mb-0 text-sm text-bold text-white">Employee ID: {dashboard?.user?.employee?.employee_identity_number}</p>
                </div>
            </div>
            <div className="d-flex p-4">
                <div className="col-6">
                    <h1 className="text-white">{dashboard?.attendance?.in}</h1>
                    <p className="text-white">Today's In Time</p>
                    <h1 className="text-white">{dashboard?.attendance?.out}</h1>
                    <p className="text-white">Today's Out Time</p>
                </div>
                <div className="col-6">
                    <div className="card">
                        <div className="card-body text-center">
                            {dashCalendar && <>
                                <h6 style={{ color: '#00a650' }}>{dashCalendar.dayName}</h6>
                                <h1 style={{ color: '#00a650' }}>{dashCalendar.day}</h1>
                                <h6 style={{ color: '#00a650' }}>{dashCalendar.monthName + ' ' + dashCalendar.year}</h6>
                            </>}
                        </div>
                    </div>
                </div>
            </div>
            <div className="d-flex px-4 pb-4">
                <div className="col-12">
                    <h5 className="text-bolder text-white">Remaining Leave: {dashboard?.totalRemainingLeaves}</h5>
                </div>
            </div>
        </div>

        <div className="row" style={{ marginTop: '25em' }}>
            <div className="col-6">
                <div className="card card-blog card-plain">
                    <div className="position-relative">
                        <NavLink to="/attendances" className="d-block border-radius-xl">
                            <img src="/assets/img/attendance.jpg" alt="" className="img-fluid border-radius-xl" />
                            <h5 className="text-center my-2">Attendance</h5>
                        </NavLink>
                    </div>
                </div>
            </div>
            <div className="col-6">
                <div className="card card-blog card-plain">
                    <div className="position-relative">
                        <NavLink to="/attendance-reconciliations" className="d-block border-radius-xl">
                            <img src="/assets/img/reconciliation.jpg" alt="" className="img-fluid border-radius-xl" />
                            <h5 className="text-center my-2">Reconciliation</h5>
                        </NavLink>
                    </div>
                </div>
            </div>
        </div>
        <div className="row mt-3">
            <div className="col-6">
                <div className="card card-blog card-plain">
                    <div className="position-relative">
                        <NavLink to="/leaves" className="d-block border-radius-xl">
                            <img src="/assets/img/leave.jpg" alt="" className="img-fluid border-radius-xl" />
                            <h5 className="text-center my-2">Leave</h5>
                        </NavLink>
                    </div>
                </div>
            </div>
            <div className="col-6">
                <div className="card card-blog card-plain">
                    <div className="position-relative">
                        <NavLink to="/holidays" className="d-block border-radius-xl">
                            <img src="/assets/img/holiday.jpg" alt="" className="img-fluid border-radius-xl" />
                            <h5 className="text-center my-2">Holiday</h5>
                        </NavLink>
                    </div>
                </div>
            </div>
        </div>
    </>;
}

export default Dashboard;
