import { useEffect, useState } from "react";
import { NavLink, useParams } from "react-router-dom";
import callFetch from "../../helpers/callFetch";
import dateFormat from "dateformat";

function HolidayShow() {
    let params = useParams();
    const [holiday, setHoliday] = useState({});

    useEffect(() => {
        callFetch("holidays/" + params.id, "GET", {}, null).then((res) => {
            if (!res.ok) return;
            setHoliday(res.data);
        });
    }, []);

    return <div className="row">
        <div className="col-12">
            <div className="card mb-4">
                <div className="card-header pb-0">
                    <div className="btn-row d-flex">
                        <h6>View Holiday</h6>
                        <NavLink to="/holidays" className="btn bg-gradient-dark ms-auto">Back</NavLink>
                    </div>
                </div>
                <div className="card-body">
                    <div className="row">
                        <div className="col-6 col-md-6">
                            <label>
                                Holiday Name
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{holiday.name}</h6>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-6 col-md-6">
                            <label>
                                Holiday From
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{dateFormat(holiday.holiday_from, "dd-mm-yyyy")}</h6>
                        </div>
                        <div className="col-6 col-md-6">
                            <label>
                                Holiday To
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{dateFormat(holiday.holiday_to, "dd-mm-yyyy")}</h6>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-md-12">
                            <label>
                                Description
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{holiday.description}</h6>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>;
}

export default HolidayShow;