import { useState } from "react";
import { useForm } from "react-hook-form";
import { Navigate, NavLink } from "react-router-dom";
import callFetch from "../../helpers/callFetch";

function LeaveCreate() {
    const [availableLeave, setAvailableLeave] = useState('-');
    const [saving, setSaving] = useState(false);
    const [submitSuccess, setSubmitSuccess] = useState(false);
    const {
        register,
        handleSubmit,
        setError,
        formState: { errors },
    } = useForm();

    const onSubmit = (formData) => {
        setSaving(true);
        formData.user_id = JSON.parse(localStorage.getItem('user')).id;
        callFetch("leaves", "POST", formData, setError).then((res) => {
            setSaving(false);
            if (!res.ok) return;
            setSubmitSuccess(true);
        });
    };

    function getAvailableLeave(leaveTypeId) {
        if (!leaveTypeId) {
            setAvailableLeave('-');
            return;
        }

        callFetch("available-leaves/" + leaveTypeId, "GET", {}, null).then((res) => {
            if (!res.ok) return;
            setAvailableLeave(res.data);
        });
    }

    return submitSuccess ? <Navigate to='/leaves' /> :
        <div className="row">
            <div className="col-12">
                <div className="card mb-4">
                    <div className="card-header pb-0">
                        <div className="btn-row d-flex">
                            <h6>Add New Leave</h6>
                            <NavLink to="/leaves" className="btn bg-gradient-dark ms-auto">Back</NavLink>
                        </div>
                    </div>
                    <div className="card-body">
                        <form className={`needs-validation ${Object.keys(errors).length ? "was-validated" : ""}`} onSubmit={handleSubmit(onSubmit)} noValidate autoComplete="off">
                            <div className="row">
                                <div className="col-md-6">
                                    <label>
                                        Leave Type *
                                    </label>
                                    <select
                                        className="form-control"
                                        {...register("leave_type_id", {
                                            required: true
                                        })}
                                        onChange={(e) => getAvailableLeave(e.target.value)}
                                        required
                                    >
                                        <option value="">--</option>
                                        <option value="1">Casual</option>
                                        <option value="2">Medical</option>
                                        <option value="3">Earned</option>
                                    </select>
                                    <div className="invalid-feedback">{errors.status && errors.status.message}</div>
                                </div>
                                <div className="col-md-6">
                                    <label>Available Leave</label>
                                    <h6 className="text-sm text-secondary mb-0">{availableLeave}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <label>
                                        Leave From *
                                    </label>
                                    <input
                                        type="date"
                                        className="form-control mb-4"
                                        placeholder="eg. 15-03-2001"
                                        {...register("leave_from", {
                                            required: true,
                                        })}
                                        required
                                    />
                                    <div className="invalid-feedback">{errors.leave_from && errors.leave_from.message}</div>
                                </div>
                                <div className="col-md-6">
                                    <label>
                                        Leave To *
                                    </label>
                                    <input
                                        type="date"
                                        className="form-control mb-4"
                                        placeholder="eg. 15-03-2001"
                                        {...register("leave_to", {
                                            required: true,
                                        })}
                                        required
                                    />
                                    <div className="invalid-feedback">{errors.leave_to && errors.leave_to.message}</div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-12">
                                    <label>
                                        Leave Reason *
                                    </label>
                                    <textarea
                                        className="form-control mb-4"
                                        placeholder="eg. Details ..."
                                        {...register("details", {
                                            required: true,
                                        })}
                                        required
                                    ></textarea>
                                    <div className="invalid-feedback">{errors.details && errors.details.message}</div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-6">
                                    <label>Attachment</label>
                                    <input type="file" className="form-control mb-4" {...register("attachment")} />
                                    <div className="invalid-feedback">{errors.attachment && errors.attachment.message}</div>
                                </div>
                            </div>
                            <div className="col-12 my-4">
                                {!saving && (
                                    <button type="submit" className="btn btn-primary">
                                        Save
                                    </button>
                                )}
                                {saving && (
                                    <button type="button" className="btn btn-disabled" disabled>
                                        Saving ...
                                    </button>
                                )}
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>;
}

export default LeaveCreate;