import firebase from 'firebase/compat/app';
import 'firebase/compat/messaging';
import callFetch from './callFetch';

function myFirebase() {
    const firebaseConfig = {
        apiKey: "AIzaSyA1JdCM4bhEIyPKojZeqnlwSAps1dqwX8Y",
        authDomain: "hajira-pwa.firebaseapp.com",
        projectId: "hajira-pwa",
        storageBucket: "hajira-pwa.appspot.com",
        messagingSenderId: "934805741636",
        appId: "1:934805741636:web:6f84387a6939211dc19c11"
    };

    firebase.initializeApp(firebaseConfig);
    // Retrieve an instance of Firebase Messaging so that it can handle background messages.
    const messaging = firebase.messaging();

    // Check if an FCM token already exists
    messaging.getToken()
        .then((token) => {
            if (token) {
                saveToken(token);
            }
            else {
                Notification.requestPermission()
                    .then((permission) => {
                        if (permission === 'granted') {
                            messaging.getToken()
                                .then((token) => {
                                    saveToken(token);
                                })
                                .catch((error) => {
                                    // console.error(error);
                                });
                        }
                    })
                    .catch((error) => {
                        // console.error(error);
                    });
            }
        })
        .catch((error) => {
            // console.error(error);
        });

    var greenHrmChannel = new BroadcastChannel('hrm');
    messaging.onMessage((payload) => {
        greenHrmChannel.postMessage({ hasUnseenNotification: true });
    }, e => { });

    function saveToken(token) {
        callFetch('fcm-token', 'POST', { fcm_token: token }, null).then((res) => {
            if (!res.ok) return;
        });
    }
}

export default myFirebase;
