import { useEffect, useRef, useState } from "react";
import GoogleMapReact from 'google-map-react';
import { Navigate, NavLink } from "react-router-dom";
import Camera, { FACING_MODES } from 'react-html5-camera-photo';
import callFetch from "../../helpers/callFetch";
import { toast } from 'react-toastify';

const AnyReactComponent = () => <div><img src="https://cdn2.iconfinder.com/data/icons/boxicons-regular-vol-3/24/bx-user-pin-64.png" alt="" /></div>;

function AttendanceCreate() {
    const streamRef = useRef(null);
    const [photo, setPhoto] = useState(null);
    const [coords, setCoords] = useState(null);
    const [refresh, setRefresh] = useState(0);
    const [submitSuccess, setSubmitSuccess] = useState(false);

    useEffect(() => {
        navigator.geolocation.getCurrentPosition(position => {
            const { latitude, longitude } = position.coords;
            setCoords({ lat: latitude, lng: longitude });
            setRefresh(refresh + 1);
        });
    }, []);

    function doCheckin() {
        callFetch("attendances", "POST", { lat: coords.lat, lng: coords.lng, photo: photo }, null).then((res) => {
            if (!res.ok) return;
            setSubmitSuccess(true);
        });
    }

    function handleTakePhoto(dataUri) {
        if (dataUri)
            callFetch("validate-user-photo?toast=false", "POST", { photo: dataUri }, null).then((res) => {
                if (!res.ok) return;

                if (!res.data) {
                    toast.error('Invalid face!');
                    return;
                }

                setPhoto(dataUri);
                stopCamera();
                setRefresh(refresh + 1);
            });
    }

    const handleCameraStart = (stream) => {
        streamRef.current = stream;
    };

    function stopCamera() {
        if (streamRef.current) {
            const tracks = streamRef.current.getTracks();

            tracks.forEach((track) => {
                track.stop();
            });
        }
    }

    useEffect(() => { }, [refresh]);

    return submitSuccess ? <Navigate to='/attendances' /> : (<div className="row">
        <div className="col-12">
            <div className="card mb-4">
                <div className="card-header pb-0">
                    <div className="btn-row d-flex">
                        <h6>Give Attendance</h6>
                        <NavLink to="/attendances" className="btn bg-gradient-dark ms-auto">Back</NavLink>
                    </div>
                </div>
                <div className="card-body">
                    <div className="row">
                        <div className="col-md-12">
                            {coords && <>
                                {!photo && <>
                                    <h6 className="text-sm text-secondary mb-2">STEP 1: Take a photo</h6>
                                    <Camera
                                        onTakePhoto={(dataUri) => { handleTakePhoto(dataUri); }}
                                        onCameraStart={handleCameraStart}
                                        isFullscreen={false}
                                        isImageMirror={true}
                                        idealFacingMode={FACING_MODES.USER}
                                    />
                                </>}
                                {photo && <>
                                    <h6 className="text-sm text-secondary mb-2">STEP 2: Check in</h6>
                                    <div style={{ height: '60vh', width: '100%' }}>
                                        <GoogleMapReact
                                            bootstrapURLKeys={{ key: "AIzaSyBe7IMO-nB5KEb2i4z9XChzGDZdOf1SEuE" }}
                                            defaultCenter={{ lat: coords.lat, lng: coords.lng }}
                                            defaultZoom={19}
                                            options={{ mapTypeId: 'roadmap', tilt: 0, zoomControlOptions: { position: 3 }, fullscreenControl: false }}
                                        >
                                            <AnyReactComponent
                                                lat={coords.lat}
                                                lng={coords.lng}
                                            />
                                        </GoogleMapReact>
                                    </div>
                                    <button type="button" onClick={() => doCheckin()} className="btn btn-primary w-100 mt-2">Check In</button>
                                </>}
                            </>}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>);
}

export default AttendanceCreate;