import { useEffect, useState } from 'react';
import DataTable from 'react-data-table-component';
import dateFormat from "dateformat";
import callFetch from '../../helpers/callFetch';
import { NavLink } from 'react-router-dom';

function HolidayIndexTable() {
    const [holidays, setHolidays] = useState([]);
    const [pageNumber, setPageNumber] = useState(1);

    function calculateDiff(date1, date2) {
        const date1Obj = new Date(date1);
        const date2Obj = new Date(date2);
        const diffTime = Math.abs(date2Obj - date1Obj);
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
        return diffDays + 1;
    }

    const tableHeadings = [
        {
            name: 'Name',
            width: '70%',
            cell: row => <div>
                <span className="text-sm font-weight-bold">Name:</span>
                <span className="text-sm"> {row.name}</span>
                <br />
                <span className="text-sm font-weight-bold">Holiday From:</span>
                <span className="text-sm"> {dateFormat(row.holiday_from, "dd-mm-yyyy")}</span>
                <br />
                <span className="text-sm font-weight-bold">Holiday To:</span>
                <span className="text-sm"> {dateFormat(row.holiday_to, "dd-mm-yyyy")}</span>
                <br />
                <span className="text-sm font-weight-bold">Total Days:</span>
                <span className="text-sm"> {calculateDiff(row.holiday_from, row.holiday_to)}</span>
            </div>
        },
        {
            name: 'Actions',
            width: '80px',
            cell: row => <div className="text-center dropstart">
                <a href="/" className="dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="fa fa-ellipsis-v text-xs"></i>
                </a>
                <ul className="dropdown-menu">
                    <li>
                        <NavLink to={'/holidays/' + row.id} className="dropdown-item">
                            View
                        </NavLink>
                    </li>
                </ul>
            </div>
        }
    ];

    useEffect(() => {
        callFetch("holidays?page=" + pageNumber, "GET", []).then((res) => {
            setHolidays(res.data);
        });
    }, [pageNumber]);

    const handlePageChange = page => {
        setPageNumber(page);
    }

    function toPages(pages) {
        const results = [];

        for (let i = 1; i <= pages; i++) {
            if (i > 5) {
                results.push(pages);
                break;
            }

            results.push(i);
        }

        return results;
    }

    // RDT exposes the following internal pagination properties
    const BootyPagination = ({
        onChangePage,
        currentPage
    }) => {
        const handleBackButtonClick = () => {
            onChangePage(currentPage - 1);
        };

        const handleNextButtonClick = () => {
            onChangePage(currentPage + 1);
        };

        const handlePageNumber = (e) => {
            onChangePage(Number(e.target.value));
        };

        const pages = holidays.last_page;
        const pageItems = toPages(pages);
        const nextDisabled = currentPage === holidays.last_page;
        const previosDisabled = currentPage === 1;

        return (
            <>
                <br />
                <p className="float-md-start pt-2 text-secondary text-xs font-weight-bolder ms-3">Showing {holidays.from} to {holidays.to} of {holidays.total} entries</p>
                <nav className="float-md-end me-2">
                    <ul className="pagination">
                        <li className="page-item">
                            <button
                                className="page-link"
                                onClick={handleBackButtonClick}
                                disabled={previosDisabled}
                                aria-disabled={previosDisabled}
                                aria-label="previous page"
                            >
                                &#60;
                            </button>
                        </li>
                        {pageItems.map((page) => {
                            const className =
                                page === currentPage ? "page-item active" : "page-item";

                            return (
                                <li key={page} className={className}>
                                    <button
                                        className="page-link"
                                        onClick={handlePageNumber}
                                        value={page}
                                    >
                                        {page}
                                    </button>
                                </li>
                            );
                        })}
                        <li className="page-item">
                            <button
                                className="page-link"
                                onClick={handleNextButtonClick}
                                disabled={nextDisabled}
                                aria-disabled={nextDisabled}
                                aria-label="next page"
                            >
                                &#62;
                            </button>
                        </li>
                    </ul>
                </nav>
                <div className="clearfix"></div>
            </>
        );
    };

    return <DataTable
        columns={tableHeadings}
        data={holidays.data}
        noDataComponent='There are no records to display'
        pagination
        highlightOnHover
        paginationComponentOptions={{ noRowsPerPage: true }}
        paginationServer
        paginationTotalRows={holidays.total}
        onChangePage={handlePageChange}
        paginationComponent={BootyPagination}
    />;
}

export default HolidayIndexTable;