import { useEffect, useState } from "react";
import { Navigate } from "react-router-dom";
import PrivateLayout from "./PrivateLayout";
import myFirebase from "../helpers/myFirebase";

function PrivateOutlet() {
    let tempAuth = localStorage.getItem('token') ? true : false;
    const [auth, setAuth] = useState(tempAuth);

    useEffect((auth) => {
        myFirebase();

        const checkAuth = () => {
            let tempAuth = localStorage.getItem('token') ? true : false;
            if (tempAuth !== auth)
                setAuth(tempAuth);
        }

        const authObserver = setInterval(checkAuth, 2000);

        return () => {
            clearInterval(authObserver);
        };
    }, []);

    return auth ? <PrivateLayout /> : <Navigate to='/' />;
}

export default PrivateOutlet;
