import { Suspense } from 'react';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import PrivateOutlet from "./components/PrivateOutlet";
import AttendanceReconciliationCreate from './pages/attendance-reconciliations/AttendanceReconciliationCreate';
import AttendanceReconciliationIndex from './pages/attendance-reconciliations/AttendanceReconciliationIndex';
import AttendanceReconciliationShow from './pages/attendance-reconciliations/AttendanceReconciliationShow';
import AttendanceCreate from './pages/attendances/AttendanceCreate';
import AttendanceIndex from './pages/attendances/AttendanceIndex';
import HolidayIndex from './pages/holidays/HolidayIndex';
import HolidayShow from './pages/holidays/HolidayShow';
import LeaveCreate from './pages/leaves/LeaveCreate';
import LeaveIndex from './pages/leaves/LeaveIndex';
import LeaveShow from './pages/leaves/LeaveShow';
import NotificationShow from './pages/notifications/NotificationShow';
import Signin from './pages/Signin';
import Dashboard from './pages/Dashboard';

function App() {
    return (
        <Suspense fallback="loading ...">
            <BrowserRouter>
                <ToastContainer theme="dark" />
                <Routes>
                    <Route path="/" element={<Signin />} />
                    <Route path="/*" element={<PrivateOutlet />}>
                        <Route path="dashboard" element={<Dashboard />} />
                        <Route path="attendances" element={<AttendanceIndex />} />
                        <Route path="attendances/create" element={<AttendanceCreate />} />
                        <Route path="leaves" element={<LeaveIndex />} />
                        <Route path="leaves/create" element={<LeaveCreate />} />
                        <Route path="leaves/:id" element={<LeaveShow />} />
                        <Route path="attendance-reconciliations" element={<AttendanceReconciliationIndex />} />
                        <Route path="attendance-reconciliations/create" element={<AttendanceReconciliationCreate />} />
                        <Route path="attendance-reconciliations/:id" element={<AttendanceReconciliationShow />} />
                        <Route path="holidays" element={<HolidayIndex />} />
                        <Route path="holidays/:id" element={<HolidayShow />} />
                        <Route path="notifications" element={<NotificationShow />} />
                    </Route>
                </Routes>
            </BrowserRouter>
        </Suspense>
    );
}

export default App;
