import { useState } from "react";
import { useForm } from "react-hook-form";
import { Navigate, NavLink } from "react-router-dom";
import callFetch from "../../helpers/callFetch";

function AttendanceReconciliationCreate() {
    const [reconciliationType, setReconciliationType] = useState(null);
    const [saving, setSaving] = useState(false);
    const [submitSuccess, setSubmitSuccess] = useState(false);
    const {
        register,
        handleSubmit,
        setError,
        formState: { errors },
    } = useForm();

    const onSubmit = (formData) => {
        setSaving(true);
        formData.user_id = JSON.parse(localStorage.getItem('user')).id;
        callFetch("attendance-reconciliations", "POST", formData, setError).then((res) => {
            setSaving(false);
            if (!res.ok) return;
            setSubmitSuccess(true);
        });
    };

    return submitSuccess ? <Navigate to='/attendance-reconciliations' /> :
        <div className="row">
            <div className="col-12">
                <div className="card mb-4">
                    <div className="card-header pb-0">
                        <div className="btn-row d-flex">
                            <h6>Add New Reconciliation</h6>
                            <NavLink to="/attendance-reconciliations" className="btn bg-gradient-dark ms-auto">Back</NavLink>
                        </div>
                    </div>
                    <div className="card-body">
                        <form className={`needs-validation ${Object.keys(errors).length ? "was-validated" : ""}`} onSubmit={handleSubmit(onSubmit)} noValidate autoComplete="off">
                            <div className="row">
                                <div className="col-md-6">
                                    <label>
                                        Reconciliation Type *
                                    </label>
                                    <select
                                        className="form-control mb-4"
                                        {...register("type", {
                                            required: true
                                        })}
                                        onChange={(e) => setReconciliationType(e.target.value)}
                                        required
                                    >
                                        <option value="">--</option>
                                        <option value="in">In Time</option>
                                        <option value="out">Out Time</option>
                                        <option value="both">Both</option>
                                    </select>
                                    <div className="invalid-feedback">{errors.type && errors.type.message}</div>
                                </div>
                                <div className="col-md-6">
                                    <label>
                                        Reconciliation Date *
                                    </label>
                                    <input
                                        type="date"
                                        className="form-control mb-4"
                                        placeholder="eg. 15-03-2001"
                                        {...register("attendance_date", {
                                            required: true,
                                        })}
                                        required
                                    />
                                    <div className="invalid-feedback">{errors.attendance_date && errors.attendance_date.message}</div>
                                </div>
                            </div>
                            <div className="row">
                                {(reconciliationType === 'in' || reconciliationType == 'both') && <div className="col-md-6">
                                    <label>
                                        In Time *
                                    </label>
                                    <input
                                        type="time"
                                        className="form-control mb-4"
                                        placeholder="eg. 15-03-2001"
                                        {...register("in_time", {
                                            required: true,
                                        })}
                                        required
                                    />
                                    <div className="invalid-feedback">{errors.in_time && errors.in_time.message}</div>
                                </div>}
                                {(reconciliationType === 'out' || reconciliationType == 'both') && <div className="col-md-6">
                                    <label>
                                        Out Time *
                                    </label>
                                    <input
                                        type="time"
                                        className="form-control mb-4"
                                        placeholder="eg. 15-03-2001"
                                        {...register("out_time", {
                                            required: true,
                                        })}
                                        required
                                    />
                                    <div className="invalid-feedback">{errors.out_time && errors.out_time.message}</div>
                                </div>}
                            </div>
                            <div className="row">
                                <div className="col-md-12">
                                    <label>
                                        Remarks *
                                    </label>
                                    <textarea
                                        className="form-control mb-4"
                                        placeholder="eg. Remarks ..."
                                        {...register("remarks", {
                                            required: true,
                                        })}
                                        required
                                    ></textarea>
                                    <div className="invalid-feedback">{errors.remarks && errors.remarks.message}</div>
                                </div>
                            </div>
                            <div className="col-12 my-4">
                                {!saving && (
                                    <button type="submit" className="btn btn-primary">
                                        Save
                                    </button>
                                )}
                                {saving && (
                                    <button type="button" className="btn btn-disabled" disabled>
                                        Saving ...
                                    </button>
                                )}
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>;
}

export default AttendanceReconciliationCreate;