import { useState } from "react";
import { useForm } from "react-hook-form";
import { Navigate } from "react-router-dom";
import callFetch from "../helpers/callFetch";
import Camera, { FACING_MODES } from 'react-html5-camera-photo';
import 'react-html5-camera-photo/build/css/index.css';

function Signin() {
    let tempAuth = localStorage.getItem('token') ? true : false;
    const [doFaceSignIn, setDoFaceSignIn] = useState(false);
    const [signinSuccess, setSigninSuccess] = useState(tempAuth);
    const { register, handleSubmit, setError, formState: { errors } } = useForm();

    const onSubmit = (formData) => {
        callFetch('login', 'POST', formData, setError)
            .then(loginData => {
                if (!loginData.ok)
                    return;

                localStorage.setItem('token', loginData.data.token);
                localStorage.setItem('user', JSON.stringify(loginData.data.user));
                // localStorage.setItem('permissions', JSON.stringify(loginData.data.permissions));
                window.location.href = process.env.REACT_APP_FRONTEND_URL + 'dashboard';
                //setSigninSuccess(true);
            });
    }

    function handleTakePhoto(dataUri) {
        // Do stuff with the photo...
        console.log(dataUri);
    }

    return signinSuccess ? <Navigate to='/dashboard' /> :
        <>
            <main className="main-content  mt-0">
                <section>
                    <div className="row m-0" style={{ background: '#00a650' }}>
                        <div className="col-md-12">
                            <div className="card card-plain">
                                <div className="card-headers">
                                    <h1 className="text-center text-white fw-normal mt-3">WELCOME!</h1>
                                    <p className="text-center my-3">
                                        <img src="/assets/img/logo.png" className="img-fluid" alt="" />
                                    </p>
                                    <h5 className="text-center text-white">Smart Attendance</h5>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="row m-0">
                        <div className="col-md-12">
                            <div className="card card-plain">
                                <div className="card-body">
                                    {!doFaceSignIn && <>
                                        <form className={`needs-validation ${Object.keys(errors).length ? 'was-validated' : ''}`} onSubmit={handleSubmit(onSubmit)} noValidate>
                                            <label>Email / Mobile No.</label>
                                            <div className="mb-3">
                                                <input type="email" className="form-control" placeholder="Email / Mobile No." {
                                                    ...register('email', {
                                                        required: true
                                                    })
                                                } required />
                                                <div className="invalid-feedback">
                                                    {errors.email && errors.email.message}
                                                </div>
                                            </div>
                                            <label>Password</label>
                                            <div className="mb-3">
                                                <input type="password" className="form-control" placeholder="Password" {
                                                    ...register('password', {
                                                        required: true
                                                    })
                                                } required />
                                                <div className="invalid-feedback">
                                                    {errors.password && errors.password.message}
                                                </div>
                                            </div>
                                            <button type="submit" className="btn btn-primary w-100">Sign
                                                in</button>
                                        </form>
                                        <div className="row d-none">
                                            <div className="col-md-12">
                                                <p className="text-center text-dark fw-bold">or</p>
                                                <button onClick={() => setDoFaceSignIn(true)} type="button" className="btn btn-primary w-100">Sign
                                                    in with face</button>
                                            </div>
                                        </div>
                                        <div className="card-footer text-center pt-0 px-lg-2 px-1 d-none">
                                            <p className="text-sm mx-auto">
                                                <a href="google.com" className="text-success font-weight-bold">Forgot password?</a>
                                            </p>
                                        </div>
                                    </>}
                                    {doFaceSignIn && <>
                                        <Camera
                                            onTakePhoto={(dataUri) => { handleTakePhoto(dataUri); }}
                                            idealFacingMode={FACING_MODES.USER}
                                        />
                                        <a href="/" className="btn btn-danger w-100">Exit</a>
                                    </>}
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
            <footer className="footer py-3">
                <div className="container">
                    <div className="row">
                        <div className="col-8 mx-auto text-center mt-1">
                            <p className="mb-0 text-secondary">
                                Copyright  {new Date().getFullYear()}. All rights reserved
                            </p>
                        </div>
                    </div>
                </div>
            </footer>
        </>;
}

export default Signin;
