import { useEffect, useState } from "react";
import dateFormat from "dateformat";
import callFetch from '../../helpers/callFetch';

function NotificationShow() {
    const [notifications, setNotifications] = useState([]);
    const [page, setPage] = useState(1);
    const [allLoaded, setAllLoaded] = useState(false);
    const [refresh, setRefresh] = useState(1);

    useEffect(() => {
        callFetch("notifications?page=" + page, "GET", {}, null).then((res) => {
            if (!res.ok) return;

            if (!res.data.next_page_url)
                setAllLoaded(true);

            let nArray = notifications;
            res.data.data.map((n) => {
                nArray.push(n);
            });
            setNotifications(nArray);
            setRefresh(refresh + 1);
        });
    }, [page]);

    function getTimeString(myDate) {
        const date = new Date(myDate);
        const now = new Date();

        const diffInMilliseconds = now.getTime() - date.getTime();
        const diffInSeconds = diffInMilliseconds / 1000;

        let formattedDate;
        if (diffInSeconds < 60) {
            formattedDate = `${Math.round(diffInSeconds)} seconds ago`;
        } else if (diffInSeconds < 3600) {
            formattedDate = `${Math.round(diffInSeconds / 60)} minutes ago`;
        } else if (diffInSeconds < 86400) {
            formattedDate = `${Math.round(diffInSeconds / 3600)} hours ago`;
        } else if (diffInSeconds < 604800) {
            formattedDate = `${Math.round(diffInSeconds / 86400)} days ago`;
        } else {
            formattedDate = dateFormat(date, 'mmmm d, yyyy, h:MM:ss TT'); // Format the date normally if it's more than a week ago
        }

        return formattedDate;
    }

    useEffect(() => { }, [refresh]);

    return <div className="row">
        <div className="col-md-12">
            <div className="card">
                <div className="card-body p-2">
                    {notifications && notifications.map((notification) => (<div className="my-2 p-3 border-radius-md" key={notification.id} style={{ background: notification.seen ? 'transparent' : '#e9ecef' }}>
                        <a href="#0">
                            <div className="d-flex py-1">
                                <div className="my-auto">
                                    <img src={process.env.REACT_APP_FRONTEND_URL + "assets/img/small-logos/bell-icon.png"} className="avatar avatar-sm bg-gradient-dark  me-3 " alt="" />
                                </div>
                                <div className="d-flex flex-column justify-content-center">
                                    <h6 className="text-sm font-weight-normal mb-1">
                                        {notification.message}
                                    </h6>
                                    <p className="text-xs text-secondary mb-0">
                                        <i className="fa fa-clock me-1" />
                                        {getTimeString(notification.created_at)}
                                    </p>
                                </div>
                            </div>
                        </a>
                    </div>))}

                    {!allLoaded && <button onClick={() => setPage(page + 1)} className="btn btn-outline-success w-100 mt-3">Load More</button>}
                </div>
            </div>
        </div>
    </div>
}

export default NotificationShow;