import { useEffect, useState } from "react";
import { NavLink } from "react-router-dom";
import callFetch from "../helpers/callFetch";

function TopNav() {
    const [hasUnseenNotification, setHasUnseenNotification] = useState(localStorage.getItem('notification'));

    useEffect(() => {
        callFetch("has-unseen-notifications", "GET", {}, null).then((res) => {
            if (!res.ok) return;
            setHasUnseenNotification(res.data);
        });

        const greenHrmChannel = new BroadcastChannel('hrm');
        greenHrmChannel.onmessage = (event) => {
            let hasNote = event.data.hasUnseenNotification ? 'true' : 'false';
            localStorage.setItem('notification', hasNote);
            setHasUnseenNotification(hasNote);
        }
    }, []);

    return (
        <>
            <div className="hajira-top-nav">
                <div id="iconNavbarSidenav" style={{ float: 'left', width: '40px' }}>
                    <i className="fas fa-bars" style={{ fontSize: '2em', color: '#ffffff', margin: '8px 10px' }}></i>
                </div>
                <div style={{ float: 'right' }}>
                    <NavLink to="/notifications" onClick={() => { localStorage.setItem('notification', 'false'); setHasUnseenNotification('false'); }}>
                        {(hasUnseenNotification === 'true') && <img src={process.env.REACT_APP_FRONTEND_URL + "assets/img/shapes/dot-mark.png"} style={{ position: 'absolute', right: '5px', top: '10px' }} alt="" />}
                        <i className="fas fa-bell" style={{ fontSize: '1.5em', color: '#ffffff', margin: '12px 10px' }}></i>
                    </NavLink>
                </div>
            </div>
            <div style={{ marginTop: '50px' }}></div>
        </>
    );
}

export default TopNav;