import { useEffect, useState } from "react";
import { NavLink, useParams } from "react-router-dom";
import callFetch from "../../helpers/callFetch";
import dateFormat from "dateformat";

function AttendanceReconciliationShow() {
    let params = useParams();
    const [attendanceReconciliation, setAttendanceReconciliation] = useState({});

    useEffect(() => {
        callFetch("attendance-reconciliations/" + params.id, "GET", {}, null).then((res) => {
            if (!res.ok) return;
            setAttendanceReconciliation(res.data);
        });
    }, []);

    function getTime(timeString) {
        if (!timeString)
            return;

        const time = new Date(`2000-01-01T${timeString}`);
        return dateFormat(time, "h:MM TT");
    }

    return <div className="row">
        <div className="col-12">
            <div className="card mb-4">
                <div className="card-header pb-0">
                    <div className="btn-row d-flex">
                        <h6>View Reconciliation</h6>
                        <NavLink to="/attendance-reconciliations" className="btn bg-gradient-dark ms-auto">Back</NavLink>
                    </div>
                </div>
                <div className="card-body">
                    <div className="row">
                        <div className="col-6 col-md-6">
                            <label>
                                Reconciliation Type
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{attendanceReconciliation.type}</h6>
                        </div>
                        <div className="col-6 col-md-6">
                            <label>
                                Reconciliation Date
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{dateFormat(attendanceReconciliation.attendance_date, "dd-mm-yyyy")}</h6>
                        </div>
                    </div>
                    <div className="row">
                        {attendanceReconciliation.in_time && <div className="col-6 col-md-6">
                            <label>
                                In Time
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{getTime(attendanceReconciliation.in_time)}</h6>
                        </div>}
                        {attendanceReconciliation.out_time && <div className="col-6 col-md-6">
                            <label>
                                Out Time
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{getTime(attendanceReconciliation.out_time)}</h6>
                        </div>}
                    </div>
                    <div className="row">
                        <div className="col-md-12">
                            <label>
                                Remarks
                            </label>
                            <h6 className="text-sm text-secondary mb-0">{attendanceReconciliation.remarks}</h6>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>;
}

export default AttendanceReconciliationShow;